.App{
  margin: 100px 0;
  text-align: center;
}
.btns > button {
  border: 2px solid lightgray;
  padding: 5px 8px;
  margin: 5px;
  border-radius: 3px;
}
.btns > button:focus{
  outline: none;
}
.App .number{
  color: brown;
}
button.add1{
  position: relative;
  overflow: hidden;
}
.dot{
  position: absolute;
  width: 6px; height: 6px;
  background: lightgray;
  opacity: 0.3;
  border-radius: 50%;

  -webkit-animation: 0.4s big forwards;

          animation: 0.4s big forwards;
}
@-webkit-keyframes big{
  0%{-webkit-transform: scale(0.1);transform: scale(0.1)}
  100%{-webkit-transform: scale(20);transform: scale(20)}
}
@keyframes big{
  0%{-webkit-transform: scale(0.1);transform: scale(0.1)}
  100%{-webkit-transform: scale(20);transform: scale(20)}
}


